import React, {useState, Fragment} from 'react';
import {connect} from 'react-redux';
import {Calendar, momentLocalizer} from 'react-big-calendar';
import {
  setModalEvent,
  setNewEvent,
  setUpdatedEvent,
  setResizedEvent,
  setEvents,
  clearModalEvent,
  deleteEvent
} from '../../store'
import 'react-big-calendar/lib/css/react-big-calendar.css';
import withDragAndDrop from 'react-big-calendar/lib/addons/dragAndDrop'
import "react-big-calendar/lib/addons/dragAndDrop/styles.css"
import moment from 'moment';
import {Modal} from '../Modal';
import './index.css';

const localizer = momentLocalizer(moment);

const formats = {
  timeGutterFormat: 'H:mm',
  agendaTimeFormat: 'H:mm',
  agendaHeaderFormat: ({start, end}, culture, local) => (
    `${local.format(start, "MMMM D")} â€” ${local.format(end, "MMMM D")}`),
  dayHeaderFormat: 'dddd MMMM Do',
};

const DragAndDropCalendar = withDragAndDrop(Calendar);

const mapStateToProps = state => ({
  modalEvent: state.calendar.modalEvent,
  events: state.calendar.events
});

export const MyCalendar = connect(mapStateToProps, {
  setModalEvent,
  setNewEvent,
  setUpdatedEvent,
  setResizedEvent,
  setEvents,
  clearModalEvent,
  deleteEvent
})(props => {
  const {
    setModalEvent,
    clearModalEvent,
    deleteEvent,
    setNewEvent,
    setUpdatedEvent,
    setResizedEvent,
    setEvents,
    modalEvent,
    events
  } = props;

  const [modal, setModal] = useState(false);
  const [isNewEvent, setIsNewEvent] = useState(false);

  const moveEvent = ({event, start, end}) => {
    const index = events.indexOf(event);
    const updatedEvent = {...event, start, end};
    const nextEvents = [...events];
    nextEvents.splice(index, 1, updatedEvent);
    setUpdatedEvent(nextEvents)
  };

  const resizeEvent = ({event, start, end}) => {
    const nextEvents = events.map(existingEvent => {
      return existingEvent.id === event.id
        ? {...existingEvent, start, end}
        : existingEvent
    });
    setResizedEvent(nextEvents)
  };

  const handleEventSave = (saveElem) => {
    const findItemIndex = events.findIndex(item => item.id === saveElem.id);
    if (findItemIndex > -1) {
      const updatedEvents = events;
      updatedEvents[findItemIndex] = saveElem;
      return setEvents(updatedEvents);
    }
    setNewEvent(saveElem);
  };

  const handleEventDelete = (deleteElem) => {
    const findIndex = events.findIndex(item => item.id === deleteElem.id);
    return deleteEvent(findIndex);
  };

  const selectSlot = (event) => {
    setIsNewEvent(true);
    event.start = event.slots[0];
    event.end = event.slots[event.slots.length - 1];
    openModal(event)
  };

  const selectEvent = (event) => {
    setIsNewEvent(false);
    openModal(event);
  };

  const openModal = (event) => {
    const id = event.id ? event.id : Date.now();
    setModal(true);
    setModalEvent({
      ...event,
      id
    })
  };

  const closeModal = () => {
    setModal(false);
    clearModalEvent();
  };

  const getEventStyles = (event, start) => {
    const style = {};
    const eventStartDate = start.getDate();
    const eventStartMonth = start.getMonth();

    const currentDate = new Date().getDate();
    const currentMonth = new Date().getMonth();

    if (eventStartDate === currentDate && eventStartMonth === currentMonth) {
      style.backgroundColor = 'green';
    } else if (eventStartDate < currentDate) {
      if (eventStartMonth <= currentMonth) {
        style.backgroundColor = 'red';
      } else {
        style.backgroundColor = 'blue';
      }
    } else if (eventStartDate > currentDate) {
      if (eventStartMonth < currentMonth) {
        style.backgroundColor = 'red';
      } else {
        style.backgroundColor = 'blue';
      }
    }
    if (event.bgColor) {
      style.backgroundColor = event.bgColor;
    }
    return {style};
  };

  return (
    events &&
    <Fragment>
      <DragAndDropCalendar style={{height: 700}}
                           localizer={localizer}
                           formats={formats}
                           events={events}
                           defaultDate={new Date()}
                           defaultView={'month'}
                           min={new Date(0, 0, 0, 10, 0, 0)}
                           max={new Date(0, 0, 0, 22, 0, 0)}
                           step={60}
                           showMultiDayTimes={true}
                           selectable
                           resizable
                           popup
                           onSelectEvent={selectEvent}
                           onSelectSlot={selectSlot}
                           onEventDrop={moveEvent}
                           onEventResize={resizeEvent}
                           eventPropGetter={getEventStyles}
      />
      <Modal
        modal={modal}
        setModalEvent={setModalEvent}
        modalEvent={modalEvent}
        handleEventSave={handleEventSave}
        handleEventDelete={handleEventDelete}
        closeModal={closeModal}
        isNewEvent={isNewEvent}
      />
    </Fragment>
  )
});